<template>
  <div>
    <div v-if="$store.state.cartLoading">Идет загрузка корзины товаров...</div>
    <div v-else-if="$store.state.cartLoadingFailed">
      Не удалось загрузить инофрмацию о товаре...
      <button type="button" @click="loadCart">Попробовать еще раз</button>
    </div>
    <div v-else-if="$store.state.cartProductsData.length === 0">Корзина пуста</div>
    <main v-else class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Корзина
            </a>
          </li>
        </ul>

        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{ products.length }} товара
        </span>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <ul class="cart__list">

            <cart-item v-for="product in products" :key="product.productId" :product="product"/>

            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">
              Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
            </p>
            <p class="cart__price">
              Итого: <span>{{ totalPrice | numberFormat }}</span>
            </p>

            <router-link tag="button" class="cart__button button button--primery" :to="{ name: 'order' }" :disabled="products.length === 0" type="submit">
              Оформить заказ
            </router-link>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters, mapActions } from 'vuex';
import CartItem from '@/components/CartItem.vue';

export default {
  components: { CartItem },
  filters: {
    numberFormat,
  },
  methods: {
    ...mapActions(['loadCart']),
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
};
</script>
