<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="product.product.image" width="120" height="120" :alt="product.product.title">
    </div>
    <h3 class="product__title">
      {{ product.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ product.productId }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" @click="amount -= 1" :disabled="amount === 1" aria-label="Убрать один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count">

      <button type="button" @click="amount += 1" aria-label="Добавить один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (product.product.price * product.amount) | numberFormat }} ₽
    </b>

    <button class="product__del button-del" @click.prevent="deleteProduct(product.productId)" type="button" aria-label="Удалить товар из корзины">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  props: ['product'],
  filters: {
    numberFormat,
  },
  computed: {
    amount: {
      get() {
        return this.product.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.product.productId, amount: value });
      },
    },
  },
  methods: {
    ...mapActions({ deleteProduct: 'deleteCartProduct' }),
  },
};
</script>
