<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" :disabled="counter === 0" @click="counter -= 1">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="counter">

    <button type="button" aria-label="Добавить один товар" @click="counter += 1">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['productAmount'],
  computed: {
    counter: {
      get() {
        return this.productAmount;
      },
      set(value) {
        this.$emit('update:productAmount', value);
      },
    },
  },
};
</script>

<style>
button {
  cursor: pointer;
}
</style>
